:root {
    --bg-1: #570000;
    --bg-2: #000000;
    --text: #FFE6E6;
    --muted: #D89C9C;
    --accent: #FF3838;
    --accent-2: #FF5C5C;
    --card: #3b0202;
    --card-2: #4a0000;
    --shadow: 0 10px 30px rgba(255, 56, 56, 0.15);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
    font-size: 16px;
}

body {
    font-family: system-ui, -apple-system, 'Segoe UI', Roboto, Ubuntu, Cantarell, 'Noto Sans', sans-serif;
    background: var(--bg-1);
    color: var(--text);
    line-height: 1.6;
    overflow-x: hidden;
    position: relative;
}

body::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(ellipse at top, var(--bg-2) 0%, var(--bg-1) 100%);
    z-index: -2;
}

body::after {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background:
        radial-gradient(2px 2px at 20px 30px, var(--accent), transparent),
        radial-gradient(2px 2px at 40px 70px, var(--accent-2), transparent),
        radial-gradient(1px 1px at 90px 40px, var(--accent), transparent),
        radial-gradient(1px 1px at 130px 80px, var(--accent-2), transparent),
        radial-gradient(2px 2px at 160px 30px, var(--accent), transparent);
    background-repeat: repeat;
    background-size: 200px 100px;
    animation: stars 20s linear infinite;
    opacity: 0.3;
    z-index: -1;
}

@keyframes stars {
    from {
        transform: translateY(0px);
    }

    to {
        transform: translateY(-100px);
    }
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.section {
    padding: 80px 0;
}

.section-title {
    font-size: 2.5rem;
    font-weight: 700;
    text-align: center;
    margin-bottom: 60px;
    color: #ffffff;
    position: relative;
}

.section-title::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 60px;
    height: 3px;
    background: linear-gradient(90deg, var(--accent), var(--accent-2));
    border-radius: 2px;
}

/* ===== HEADER & NAVIGATION ===== */
#navbar {
    position: fixed;
    top: 0;
    width: 100%;
    background: rgba(0, 0, 0, 0.9);
    backdrop-filter: blur(10px);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    z-index: 1000;
    transition: all 0.3s ease;
}

.nav-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 20px;
    max-width: 1200px;
    margin: 0 auto;
}

.logo {
    font-size: 1.5rem;
    font-weight: 700;
    color: var(--text);
    text-decoration: none;
    z-index: 1001;
}

.logo-accent {
    color: var(--accent);
}

.nav-menu {
    display: flex;
    list-style: none;
    gap: 30px;
}

.nav-link {
    color: #888888;
    text-decoration: none;
    font-weight: 500;
    transition: all 0.3s ease;
    position: relative;
    padding: 5px 0;
}

.nav-link.active,
.nav-link:hover {
    color: white;
}

.nav-link::after {
    content: '';
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 0;
    height: 2px;
    background: var(--accent);
    transition: width 0.3s ease;
}

.nav-link.active::after,
.nav-link:hover::after {
    width: 100%;
}

.nav-social {
    display: flex;
    gap: 15px;
}

.social-icon {
    color: #888888;
    transition: all 0.3s ease;
    padding: 8px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.social-icon:hover {
    color: white;
    transform: scale(1.1);
    box-shadow: 0 0 15px rgba(255, 255, 255, 0.3);
}

.hamburger {
    display: none;
    flex-direction: column;
    cursor: pointer;
    gap: 4px;
    z-index: 1001;
    padding: 10px;
}

.hamburger span {
    width: 25px;
    height: 3px;
    background: var(--text);
    transition: all 0.3s ease;
    border-radius: 2px;
}

.hamburger.active span:nth-child(1) {
    transform: rotate(45deg) translate(6px, 6px);
}

.hamburger.active span:nth-child(2) {
    opacity: 0;
}

.hamburger.active span:nth-child(3) {
    transform: rotate(-45deg) translate(6px, -6px);
}

/* ===== HERO SECTION ===== */
.hero {
    min-height: 100vh;
    display: flex;
    align-items: center;
    padding: 120px 0 60px;
    position: relative;
    overflow: hidden;
}

.hero-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
    align-items: center;
}

.hero-text {
    animation: slideInLeft 1s ease-out;
}

.greeting {
    color: var(--muted);
    font-size: 1.1rem;
    margin-bottom: 15px;
}

.hero-title {
    font-size: 3.5rem;
    font-weight: 700;
    margin-bottom: 20px;
    line-height: 1.2;
    min-height: 60px;
}

.cursor {
    color: var(--accent);
    animation: blink 1s infinite;
}

@keyframes blink {

    0%,
    50% {
        opacity: 1;
    }

    51%,
    100% {
        opacity: 0;
    }
}

.hero-quote {
    color: var(--muted);
    font-size: 1.1rem;
    margin-bottom: 35px;
    max-width: 500px;
}

.hero-social {
    display: flex;
    gap: 15px;
}

.hero-social .social-link {
    color: var(--muted);
    padding: 10px;
    border-radius: 50%;
    transition: all 0.3s ease;
    background: rgba(87, 0, 0, 0.5);
    backdrop-filter: blur(5px);
    display: flex;
    align-items: center;
    justify-content: center;
}

.hero-social .social-link:hover {
    color: white;
    transform: scale(1.1);
    background: rgba(255, 56, 56, 0.3);
    box-shadow: 0 0 20px rgba(255, 56, 56, 0.3);
}

.hero-visual {
    display: flex;
    justify-content: center;
    animation: slideInRight 1s ease-out;
}

/* Аватарка в солнышке */
.avatar-container {
    position: relative;
    width: 500px;
    height: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.sun-core {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 4px solid #FFD700;
    box-shadow:
        0 0 40px rgba(255, 215, 0, 0.8),
        0 0 80px rgba(255, 215, 0, 0.5),
        0 0 120px rgba(255, 215, 0, 0.3),
        inset 0 0 30px rgba(255, 215, 0, 0.4);
    animation: sunGlow 3s ease-in-out infinite alternate;
    cursor: pointer;
    transition: transform 0.3s ease;
    overflow: hidden;
    z-index: 1;
}

.sun-core img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
    display: block;
}

.sun-core:hover {
    transform: translate(-50%, -50%) scale(1.1);
}

/* Кнопки на орбитах */
.orbit-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 65px;
    height: 65px;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.3s ease;
    transform-origin: 0 0;
    border: none;
    background: rgba(255, 255, 255, 0.15);
    backdrop-filter: blur(8px);
    padding: 0;
    overflow: hidden;
    z-index: 2;
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

/* ЛОГОТИПЫ - занимают максимум пространства */
.orbit-icon img {
    width: 100% !important;
    height: 100% !important;
    border-radius: 50%;
    object-fit: contain !important;
    display: block;
    transition: transform 0.3s ease;
}

/* Эффекты при наведении */
.orbit-icon:hover {
    background: rgba(255, 56, 56, 0.4);
    transform: scale(1.15);
    box-shadow:
        0 0 20px rgba(255, 56, 56, 0.6),
        0 0 40px rgba(255, 56, 56, 0.3);
}

.orbit-icon:hover img {
    transform: scale(1.1);
}

.orbit-icon:hover {
    background: rgba(255, 56, 56, 0.4);
    transform: scale(1.2);
    box-shadow: 0 0 10px rgba(255, 56, 56, 0.5);
}

.orbit-icon:hover img {
    transform: scale(1.1);
}

@media screen and (max-width: 768px) {
    .orbit-icon {
        width: 45px;
        height: 45px;
        padding: 6px;
    }

    .orbit-icon img {
        width: 100%;
        height: 100%;
    }
}

@media screen and (max-width: 480px) {
    .orbit-icon {
        width: 40px;
        height: 40px;
        padding: 5px;
    }
}

.orbit-icon:hover {
    background: rgba(255, 56, 56, 0.4);
    transform: scale(1.2);
    box-shadow: 0 0 10px rgba(255, 56, 56, 0.5);
}

@keyframes sunGlow {
    0% {
        box-shadow:
            0 0 50px rgba(255, 215, 0, 0.9),
            0 0 100px rgba(255, 215, 0, 0.6),
            0 0 150px rgba(255, 215, 0, 0.4),
            inset 0 0 40px rgba(255, 215, 0, 0.5);
    }

    100% {
        box-shadow:
            0 0 60px rgba(255, 215, 0, 1),
            0 0 120px rgba(255, 215, 0, 0.8),
            0 0 180px rgba(255, 56, 56, 0.5),
            inset 0 0 50px rgba(255, 215, 0, 0.7);
    }
}

@keyframes slideInLeft {
    from {
        opacity: 0;
        transform: translateX(-50px);
    }

    to {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes slideInRight {
    from {
        opacity: 0;
        transform: translateX(50px);
    }

    to {
        opacity: 1;
        transform: translateX(0);
    }
}

.glow-genz-button {
    background: transparent;
    border: 2px solid var(--accent);
    border-radius: 12px;
    color: var(--accent);
    font-weight: 700;
    font-size: 18px;
    padding: 12px 36px;
    cursor: pointer;
    transition: all 0.3s ease;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    font-family: inherit;
    position: relative;
    overflow: hidden;
    display: inline-block;
}

.glow-genz-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(45deg, var(--accent), var(--accent-2), #FF7F7F, #FF3838);
    background-size: 400% 400%;
    animation: glowGradient 4s ease infinite;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.glow-genz-button:hover {
    color: white;
    border-color: var(--accent-2);
    box-shadow: 0 0 25px rgba(255, 56, 56, 1), 0 0 50px rgba(255, 92, 92, 0.8);
    transform: scale(1.05);
}

.glow-genz-button:hover::before {
    opacity: 1;
}

@keyframes glowGradient {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

/* ===== ABOUT ME SECTION ===== */
.about-content {
    display: flex;
    flex-direction: column;
    gap: 0;
    margin-top: 60px;
}

.about-top-row {
    display: grid;
    grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
    gap: 80px;
    margin-bottom: 60px;
    align-items: stretch;
}

.code-column {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.story-column {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.story-timeline {
    flex: 1;
    height: 100%;
}

/* Правая колонка - код блок */
.code-column .code-animation {
    height: auto;
    min-height: auto;
    max-height: none;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 20px 25px 80px;
    background: rgba(0, 0, 0, 0.5);
    border: 1px solid rgba(255, 56, 56, 0.3);
    border-radius: 12px;
    font-family: 'Courier New', monospace;
    font-size: 0.9rem;
    color: var(--accent);
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
    line-height: 1.4;
    box-sizing: border-box;
    overflow: hidden;
    flex: 1;
    position: relative;
}

.code-column .code-animation::-webkit-scrollbar {
    display: none;
}

.code-column .code-animation {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.code-animation::after {
    content: '';
    position: absolute;
    bottom: 50px;
    left: 0;
    right: 0;
    height: 40px;
    background: linear-gradient(to top,
            rgba(87, 0, 0, 0.8) 0%,
            rgba(87, 0, 0, 0.5) 50%,
            transparent 100%);
    pointer-events: none;
    border-radius: 0 0 12px 12px;
    z-index: 1;
}

.code-line {
    margin-bottom: 8px;
    position: relative;
    z-index: 2;
}

.indent {
    padding-left: 20px;
}

.indent.indent {
    padding-left: 40px;
}

.code-line:first-child,
.code-line:nth-child(17),
.code-line:nth-child(24) {
    color: #D89C9C;
    font-style: italic;
}

.code-line:nth-child(4),
.code-line:nth-child(6),
.code-line:nth-child(8),
.code-line:nth-child(10),
.code-line:nth-child(13),
.code-line:nth-child(15) {
    color: #FF5C5C;
}

.code-terminal-footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
    border-top: 1px solid rgba(255, 56, 56, 0.3);
    border-radius: 0 0 12px 12px;
    padding: 12px 25px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 3;
    font-family: 'Courier New', monospace;
    font-size: 0.85rem;
}

.terminal-status {
    color: #4CAF50;
    display: flex;
    align-items: center;
    gap: 8px;
}

.terminal-status::before {
    content: '●';
    font-size: 1rem;
    animation: pulse 2s infinite;
}

@keyframes pulse {

    0%,
    100% {
        opacity: 1;
    }

    50% {
        opacity: 0.5;
    }
}

.terminal-prompt {
    color: #FF5C5C;
}

.terminal-cursor {
    color: #D89C9C;
    animation: blink 1s infinite;
}

.full-width-story {
    width: 100%;
    padding-top: 30px;
    margin-top: -10px;
}

.story-timeline {
    max-width: 100%;
    width: 100%;
}

.story-section {
    margin-bottom: 40px;
    position: relative;
    padding-left: 30px;
    border-left: 2px solid rgba(255, 56, 56, 0.3);
}

.story-section:last-child {
    margin-bottom: 0;
}

.story-section::before {
    content: '';
    position: absolute;
    left: -8px;
    top: 0;
    width: 14px;
    height: 14px;
    background: var(--accent);
    border-radius: 50%;
    box-shadow: 0 0 10px var(--accent);
}

.story-title {
    color: #ffffff;
    font-size: 1.5rem;
    margin-bottom: 15px;
    font-weight: 600;
    display: flex;
    align-items: center;
    gap: 10px;
}

.story-title svg {
    color: var(--accent);
    flex-shrink: 0;
}

.story-content {
    color: #D89C9C;
    line-height: 1.8;
    text-align: justify;
    margin-bottom: 20px;
}

.story-content p {
    margin-bottom: 15px;
}

.story-highlight {
    background: rgba(255, 56, 56, 0.1);
    border-left: 3px solid var(--accent);
    padding: 20px;
    border-radius: 8px;
    margin: 25px 0;
    color: #FFE6E6;
}

.story-quote {
    font-style: italic;
    color: #FF5C5C;
    text-align: center;
    padding: 20px;
    margin: -20px auto 40px !important;
    max-width: 700px;
    font-size: 1.2rem;
    background: rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 56, 56, 0.2);
    border-radius: 12px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.story-stats {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 20px;
    margin: 40px 0;
}

.stat-item {
    background: rgba(255, 56, 56, 0.1);
    border: 1px solid rgba(255, 56, 56, 0.3);
    border-radius: 12px;
    padding: 20px;
    text-align: center;
    transition: all 0.3s ease;
}

.stat-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 20px rgba(255, 56, 56, 0.2);
}

.stat-number {
    font-size: 2rem;
    font-weight: 700;
    color: var(--accent);
    margin-bottom: 5px;
}

.stat-label {
    font-size: 0.9rem;
    color: var(--muted);
}

.skills {
    margin-top: 50px;
}

.skills h3 {
    color: var(--text);
    margin-bottom: 20px;
    font-size: 1.2rem;
}

.skills-grid {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.skill-tag {
    background: rgba(255, 56, 56, 0.1);
    color: var(--accent);
    padding: 8px 16px;
    border-radius: 20px;
    font-size: 0.9rem;
    border: 1px solid rgba(255, 56, 56, 0.3);
    transition: all 0.3s ease;
}

.skill-tag:hover {
    background: var(--accent);
    color: white;
    transform: scale(1.05);
}

@keyframes slideInUp {
    from {
        opacity: 0;
        transform: translateY(50px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* ===== PROJECTS SECTION ===== */
.projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 40px;
    margin-top: 60px;
}

.project-card {
    background: rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(255, 56, 56, 0.2);
    border-radius: 12px;
    overflow: hidden;
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
}

.project-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 20px 40px rgba(255, 56, 56, 0.2);
    border-color: var(--accent);
}

.project-image img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    display: block;
}

.project-content {
    padding: 25px;
}

.project-content h3 {
    color: var(--text);
    margin-bottom: 15px;
    font-size: 1.3rem;
}

.project-content p {
    color: var(--muted);
    margin-bottom: 20px;
    line-height: 1.6;
}

.project-tech {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    margin-bottom: 20px;
}

.project-tech span {
    background: rgba(255, 56, 56, 0.1);
    color: var(--accent);
    padding: 4px 12px;
    border-radius: 15px;
    font-size: 0.8rem;
    border: 1px solid rgba(255, 56, 56, 0.3);
}

.project-links {
    display: flex;
    gap: 15px;
}

.project-link {
    color: var(--accent);
    text-decoration: none;
    font-weight: 500;
    transition: all 0.3s ease;
    padding: 8px 16px;
    border: 1px solid var(--accent);
    border-radius: 6px;
    display: inline-block;
}

.project-link:hover {
    background: var(--accent);
    color: white;
}

/* ===== CONTACT SECTION ===== */
.contact-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
    margin-top: 60px;
}

.contact-info p {
    color: var(--muted);
    margin-bottom: 30px;
    line-height: 1.8;
}

.contact-details {
    margin-bottom: 30px;
}

.contact-item {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-bottom: 20px;
    color: var(--muted);
}

.contact-item svg {
    color: var(--accent);
    flex-shrink: 0;
}

.contact-item span {
    font-weight: 500;
}

.social-links {
    display: flex;
    gap: 20px;
}

.social-link {
    color: var(--muted);
    transition: all 0.3s ease;
    padding: 12px;
    border-radius: 50%;
    background: rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(255, 56, 56, 0.2);
    display: flex;
    align-items: center;
    justify-content: center;
}

.social-link:hover {
    color: var(--accent);
    transform: scale(1.1);
    box-shadow: 0 0 20px rgba(255, 56, 56, 0.3);
}

.contact-form {
    background: rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(255, 56, 56, 0.2);
    border-radius: 12px;
    padding: 30px;
    backdrop-filter: blur(10px);
}

.form-group {
    margin-bottom: 20px;
}

.form-group input,
.form-group textarea {
    width: 100%;
    padding: 15px;
    background: rgba(0, 0, 0, 0.5);
    border: 1px solid rgba(255, 56, 56, 0.3);
    border-radius: 8px;
    color: var(--text);
    font-family: inherit;
    transition: all 0.3s ease;
    font-size: 1rem;
}

.form-group input:focus,
.form-group textarea:focus {
    outline: none;
    border-color: var(--accent);
    box-shadow: 0 0 10px rgba(255, 56, 56, 0.3);
}

.form-group input::placeholder,
.form-group textarea::placeholder {
    color: var(--muted);
}

/* ===== ANIMATIONS ===== */
.slide-in-left {
    transform: translateX(-30px);
    opacity: 0;
    transition: all 1.5s ease;
}

.slide-in-right {
    transform: translateX(30px);
    opacity: 0;
    transition: all 1.5s ease;
}

.slide-in-up {
    transform: translateY(30px);
    opacity: 0;
    transition: all 1.5s ease;
}

/* ===== NOTIFICATIONS ===== */
.custom-notification {
    position: fixed;
    top: 20px;
    right: 20px;
    z-index: 10000;
    max-width: 400px;
    width: 90%;
    transform: translateX(150%);
    transition: transform 0.3s cubic-bezier(0.68, -0.55, 0.27, 1.55);
    font-family: inherit;
}

.custom-notification.show {
    transform: translateX(0);
}

.notification-content {
    background: var(--card);
    border-left: 4px solid;
    border-radius: 8px;
    padding: 15px 20px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 15px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 56, 56, 0.2);
}

.custom-notification.success .notification-content {
    border-left-color: #4CAF50;
    background: rgba(87, 0, 0, 0.95);
}

.custom-notification.error .notification-content {
    border-left-color: var(--accent);
    background: rgba(87, 0, 0, 0.95);
}

.notification-text {
    flex: 1;
    color: var(--text);
    font-weight: 500;
    white-space: pre-line;
    line-height: 1.4;
}

.notification-close {
    background: none;
    border: none;
    color: var(--muted);
    font-size: 24px;
    cursor: pointer;
    padding: 0;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: all 0.3s ease;
    flex-shrink: 0;
    line-height: 1;
}

.notification-close:hover {
    background: rgba(255, 255, 255, 0.1);
    color: var(--accent);
}

/* ===== MOBILE ADAPTATION ===== */
@media screen and (max-width: 1100px) {
    .container {
        width: 92% !important;
        max-width: 92% !important;
        margin: 0 auto !important;
        padding: 0 15px !important;
    }

    .hamburger {
        display: flex !important;
    }

    .nav-menu {
        position: fixed;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, 0.98);
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 20px;
        transition: left 0.3s ease;
        z-index: 1000;
        gap: 20px;
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-social {
        display: none !important;
    }

    .nav-link {
        font-size: 1.3rem;
        padding: 15px 0;
        width: 100%;
        text-align: center;
        color: var(--text);
        border-bottom: 1px solid rgba(255, 56, 56, 0.2);
    }

    .hero-content {
        grid-template-columns: 1fr !important;
        gap: 40px !important;
        text-align: center;
    }

    .hero-title {
        font-size: 2.2rem !important;
        line-height: 1.2 !important;
    }

    .hero-quote {
        margin: 0 auto 25px !important;
    }

    .avatar-container {
        width: 300px !important;
        height: 300px !important;
        margin: 0 auto;
    }

    .sun-core {
        width: 150px !important;
        height: 150px !important;
    }

    .orbit-icon {
        width: 40px !important;
        height: 40px !important;
    }

    .about-top-row {
        display: flex !important;
        flex-direction: column !important;
        width: 100% !important;
        gap: 40px !important;
    }

    .code-column {
        order: 1 !important;
        width: 100% !important;
    }

    .story-column {
        order: 2 !important;
        width: 100% !important;
    }

    .code-animation {
        font-size: 0.85rem !important;
        padding: 15px 15px 60px !important;
    }

    .projects-grid {
        grid-template-columns: 1fr !important;
    }

    .contact-content {
        grid-template-columns: 1fr !important;
        gap: 40px !important;
    }
}

@media screen and (max-width: 768px) {
    .hero {
        padding: 100px 0 40px;
    }

    .hero-title {
        font-size: 2rem !important;
    }

    .section-title {
